@font-face {
  font-family: van-icon-result;
  src: url(data:font/truetype;charset=utf-8;base64,)
    format('truetype');
  font-weight: 400;
  font-style: normal;
}
.van-icon-result {
  font-family: van-icon-result !important;
  font-size: 32px;
  font-style: normal;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.van-icon-result-info:before {
  content: '\e6ce';
}
.van-icon-result-wait:before {
  content: '\e743';
}
.van-icon-result-success:before {
  content: '\e68b';
}
.van-icon-result-error:before {
  content: '\e6cb';
}
